import toast from './toast';

function changeTitle(title) {
  document.title = title;
  // hack在微信等webview中无法修改document.title的情况
  let $iframe = document.createElement('iframe');
  $iframe.style.display = 'none';
  $iframe.src = '/favicon.ico';
  $iframe.onload = () => setTimeout(() => $iframe.onload = null, 0);
  document.body.appendChild($iframe);
}

const isWechat = !!navigator.userAgent.toLowerCase().match(/MicroMessenger/i);

const isAlipay = !!navigator.userAgent.toLowerCase().match(/AlipayClient/i);

const isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;

/**
 * 动态加载script脚本, 会根据id进行判重
 * @param src 脚本数据数组或者对象 [{id: '', url: ''},] || {id: '', url: ''}
 * @param loading 是否显示toast
 * @returns {Promise}
 */
const getScript = (src, error, loading = true,) => {
  loading && toast.loading();
  return new Promise((resolve, reject) => {
    if (!Array.isArray(src)) {
      src = [src];
    }

    let count = 0;
    let length = src.length;
    let hasError = false;

    const onload = () => {
      count++;
      if (length === count) {
        toast.hide();
        if (hasError) {
          reject();
        } else {
          resolve();
        }
      }
    };

    const onerror = () => {
      count++;
      hasError = true;
      if (length === count) {
        toast.hide();
        reject();
      }
    };

    for (let item of src) {
      if (item.id && window.document.getElementById(item.id)) {
        onload();
      }

      const script = window.document.createElement('script');
      script.id = item.id;
      script.src = item.url;
      script.onload = onload;
      script.onerror = onerror;

      window.document.body.appendChild(script);
    }
  });
};

export default {
  changeTitle,
  isWechat,
  isAlipay,
  getScript,
  isIOS
};



