<template>
  <div class="action-sheet-container" v-show="visible">
    <transition name="fade">
      <div class="mask" v-if="isShow" @click="hide()"></div>
    </transition>
    <transition name="fade-slide">
      <div class="content" v-if="isShow">
        <p class="title">{{title}}</p>
        <slot>哈哈哈</slot>
        <div class="close-btn" @click="hide()">关闭</div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'ActionSheet',
    props: {
      title: String,
      visible: Boolean
    },
    data(){
      return {
        isShow: this.visible
      }
    },
    computed: {},
    methods: {
      show(){
        this.isShow = true;
        this.$emit('shown');
      },
      hide(){
        this.isShow = false;
        setTimeout(()=>{
          this.$emit('hidden');
        },300);
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~@/style/mixin";

  .action-sheet-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    z-index: 15;
    .mask {
      position: absolute;
      opacity: 0.7;
      width: 100%;
      height: 100%;
      background-color: black;
    }
    .content {
      width: 100%;
      bottom: 0;
      height: 65%;
      text-align: center;
      background-color: $color-fff;
      position: absolute;
      .title {
        @include Height(50px);
        border-bottom: solid 1px $color-f5;
      }
      .close-btn {
        position: absolute;
        bottom: 0;
        transform: translateY(0);
        background-color: $mainColor;
        @include Height(50px);
        width: 100%;
        color: $color-fff;
      }
    }
    .fade-slide-enter-active, .fade-slide-leave-active,.fade-enter-active,.fade-leave-active {
      transition: all .3s
    }
    .fade-enter, .fade-leave-to {
      opacity: 0;
    }
    .fade-slide-enter, .fade-slide-leave-to {
      transform: translateY(100%);
    }
  }




</style>



