function pad2(n) {  // always returns a string
  return (n < 10 ? '0' : '') + n;
}


if (!Date.prototype.now) {
  Date.now = function () {
    return (new Date).getTime();
  }
}

if (!Date.prototype.format) {
  // 对Date的扩展，将 Date 转化为指定格式的String
  // 月(M)、日(d)、小时(h)、分(m)、秒(s) 可以用 1-2 个占位符，
  // 年(y)可以用 1-4 个占位符，毫秒(S)只能用 1 个占位符(是 1-3 位的数字)
  // 例子：
  // (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
  // (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
  Date.prototype.format = function (fmt) {
    let reg = {
      "M+": this.getMonth() + 1, //月份
      "d+": this.getDate(), //日
      "h+": this.getHours() % 12, //小时 12 // TODO: 需要确认下12小时制的时候 0点显示0 还是12?
      "H+": this.getHours(), //小时 24
      "m+": this.getMinutes(), //分
      "s+": this.getSeconds(), //秒
    };

    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    }

    for (let k in reg) {
      if (new RegExp("(" + k + ")").test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (reg[k]) : pad2(reg[k]));
      }
    }

    return fmt;
  };

  Date.prototype.yesterday = function () {
    let d = new Date();
    d.setDate(d.getDate() - 1);
    return d;
  };

  Date.prototype.dayBeforeYesterday = function () {
    let d = new Date();
    d.setDate(d.getDate() - 2);
    return d;
  };

  Date.prototype.equalDay = function (date) {
    return date.toDateString() === this.toDateString();
  };

  Date.prototype.isToday = function () {
    return this.equalDay(new Date());
  };

  Date.prototype.isYesterday = function () {
    return this.equalDay(this.yesterday());
  };

  Date.prototype.isDayBeforeYesterday = function () {
    return this.equalDay(this.dayBeforeYesterday());
  };
}



