<template>
  <div class="page">
    <div class="amount-input-container">
      <div class="amount-input-content" @click="toggleFocus(1)">
        <p class="label">消费总金额(元)</p>
        <div class="amount-input">{{totalAmount}}</div>
        <p class="cursor" v-show="totalAmountFocused"></p>
      </div>

      <div class="horizontal-line"></div>

      <div class="amount-input-content" @click="toggleFocus(2)">
        <p class="label">不参与优惠金额(元)</p>
        <div class="amount-input">{{unDiscountAmount}}</div>
        <p class="cursor" v-show="unDiscountAmountFocused"></p>
      </div>
    </div>
    <div class="vip-container" v-if="window.isVipCustomer && window.isVipCustomer">
      <p class="title">会员专享</p>
      <NavRow title="店铺优惠"
              v-if="window.hasActivity"
              :text="selectedActivityName"
              placeholder="不使用优惠券"
              disabledText="无可用优惠券"
              :disabled="!activityList.length"
              :action="()=>{$refs.actionSheet.show()}"/>
      <NavRow title="优惠券"
              v-if="window.hasCoupon"
              :text="$store.state.couponName"
              :action="()=>{$router.push({path: 'Coupon'})}"
              placeholder="不使用优惠券"
              disabledText="无可用优惠券"
              :disabled="!$store.state.couponList.length"/>
      <NavRow title="会员折扣" v-if="window.hasDiscount" :text="discount" :noArrow="true"/>
      <p class="hint">说明:折扣互斥取最低折扣结算</p>
      <RadioRow
        title="会员支付"
        :isChecked="balancePay"
        :disabled="!balanceEnough"
        :text="balanceAmount"
        @checked="toggleBalancePay">
        <a :href="window.vipTopUpLocation" style='margin-left:2px'>去充值</a>
      </RadioRow>
    </div>
    <div class="register-link-container" v-if="window.isVipMerchant && !window.isVipCustomer">
      <a href="#" class="register-link">注册会员享优惠</a>
    </div>
    <div class="pay-bar">
      <p class="amount-info"><span>优惠后</span><span style="font-size: 20px">{{finalAmountText}}</span></p>
      <p class="pay-btn" :class="{disabled:!finalAmount}" @click.stop="payAction">去支付</p>
    </div>
    <KeyBoard title="店铺优惠" ref="keyboard"
              :visible="showKeyBoard"
              :valid="keyboardValid"
              @shown="()=>{showKeyBoard = true}"
              @hidden="()=>{showKeyBoard = false}"
              @onInput="inputAmount"
              @onConfirm="confirmAmount"
              @onDelete="deleteAmount"/>
    <ActionSheet title="店铺优惠" ref="actionSheet"
                 v-if="window.hasActivity"
                 :visible="showActivityList"
                 @shown="()=>{showActivityList = true}"
                 @hidden="()=>{showActivityList = false}">
      <RadioRow v-for="item in activityList"
                :key="item.id" :tag="item.tag"
                :title="item.content" :isChecked="getCheckedStatus(item.id)"
                @checked="updateSelectedActivity(item.id,item.content)"/>
      <RadioRow title="不使用优惠券" :isChecked="getCheckedStatus()" @checked="updateSelectedActivity"/>
    </ActionSheet>

  </div>
</template>

<script>
  export default {
    data () {
      return {
        finalAmount: "",
        totalAmount: "",
        unDiscountAmount: "",
        useCoupon: true,
        activityId: "",
        joinActivity: true,
        totalAmountFocused: true,
        unDiscountAmountFocused: false,
        balancePay: false,
        selectedActivityName: '',
        discount: window.vipDiscount,
        vipBalance: window.vipBalance,
        showActivityList: false,
        showKeyBoard: true,
        activityList: [],
        window: window
      }
    },
    watch: {
      couponId(val, oldVal){
        if (!oldVal || val === oldVal) return;
        if (!val) this.useCoupon = false;
        this.updateFinalAmount();
      }
    },
    computed: {
      couponId(){
        return this.$store.state.couponId
      },
      finalAmountText(){
        return this.finalAmount ? `￥${Number(this.finalAmount).toYuan()}` : '计算中'
      },
      balanceEnough(){
        return !(Number(this.vipBalance) < Number(this.finalAmount));
      },
      balanceAmount(){
        return `余额:${Number(this.vipBalance).toYuan()}元`
      },
      keyboardValid(){
        if (!this.totalAmount) {
          return false
        }
        if (Number(Number(this.totalAmount).toCent()) < Number(Number(this.unDiscountAmount || "0").toCent())) {
          return false;
        }
        if (this.totalAmountFocused) {
          return /^(([1-9]\d{0,8})|0)(\.\d{1,2})?$/.test(this.totalAmount);
        }
        if (this.unDiscountAmountFocused) {
          return !this.unDiscountAmount || /^(([1-9]\d{0,8})|0)(\.\d{1,2})?$/.test(this.unDiscountAmount);
        }
        return false;
      }
    },
    methods: {
      updateSelectedActivity(id = "", name = ""){
        if (id === "") this.joinActivity = false;
        this.activityId = id;
        this.selectedActivityName = name;
        setTimeout(() => {
          this.$refs.actionSheet.hide();
        }, 200)
      },

      getCheckedStatus(id = ""){
        return id === this.$store.state.activityId;
      },

      toggleFocus(type){
        if (type === 1) {
          this.totalAmountFocused = true;
          this.unDiscountAmountFocused = false;
          this.$refs.keyboard.show(true);
        } else if (type === 2) {
          this.totalAmountFocused = false;
          this.unDiscountAmountFocused = true;
          this.$refs.keyboard.show(true);
        } else {
          this.totalAmountFocused = false;
          this.unDiscountAmountFocused = false;
          this.$refs.keyboard.close(false);
        }
      },

      toggleBalancePay(){
        this.balancePay = !this.balancePay;
      },

      isValidInput(amount, newNum){
        //输入长度超过10位
        if (amount.length > 9)
          return false;
        //小数点在第一位
        if (newNum == '.' && amount.length == 0)
          return false;
        //首字符为0的情况下，第二位数值不为点
        if (newNum != '.' && amount.length == 1 && amount[0] == '0')
          return false;
        //不能出现2个点
        if (newNum == '.' && amount.indexOf('.') > 0)
          return false;
        //小数点后只允许存在2位
        var index = amount.indexOf('.');
        if (index > 0) {
          if ((amount.length - index) == 3)
            return false
        }
        return true;
      },

      inputAmount(value){
        if (this.totalAmountFocused) {
          if (this.isValidInput(this.totalAmount.split(''), value))
            this.totalAmount += value;
        } else if (this.unDiscountAmountFocused) {
          if (this.isValidInput(this.unDiscountAmount.split(''), value))
            this.unDiscountAmount += value;
        }
      },

      deleteAmount(){
        if (this.totalAmountFocused && this.totalAmount) {
          this.totalAmount = this.totalAmount.replace(/.$/, "");
        } else if (this.unDiscountAmountFocused && this.unDiscountAmount) {
          this.unDiscountAmount = this.unDiscountAmount.replace(/.$/, "");
        }
      },

      confirmAmount(){
        this.updateFinalAmount();
      },

      getCheckedId(items){
        for (let item of items) {
          if (item.isChecked) return item.id;
        }
        return "";
      },
      getCouponAmount(items){
        for (let item of items) {
          if (item.isChecked) return `已选一张 共抵${Number(item.discountValue).toYuan()}元`
        }
        return "";
      },

      updateFinalAmount(){
        let data = {
          totalAmount: Number(this.totalAmount).toCent(),
          unDiscountAmount: Number(this.unDiscountAmount).toCent(),
          activityId: this.activityId,
          couponId: this.$store.state.couponId,
          cardNo: window.cardNo,
          userId: window.userId,
          useCoupon: this.useCoupon,
          joinActivity: this.joinActivity
        };
        this.totalAmountFocused = false;
        this.unDiscountAmountFocused = false;

        this.$http.request("h5_vipv3_check_promotion", data)
          .then(data => {
            this.activityList = data.activityList;
            this.$store.commit('setCouponList', data.couponList);
            this.$store.commit('setCouponId', this.getCheckedId(data.couponList));
            this.activityId = this.getCheckedId(data.activityList);
            this.$store.commit('setCouponName', this.getCouponAmount(data.couponList));
            this.finalAmount = data.finalAmount;
          });
      },
      payAction(){
        if (this.finalAmount) {
          let data = {
            totalAmount: Number(this.totalAmount).toCent(),
            unDiscountAmount: Number(this.unDiscountAmount).toCent(),
            activityId: this.activityId,
            couponId: this.$store.state.couponId,
            isBalancePay: this.isBalancePay
          };
          console.log(data);
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~@/style/mixin";

  .page {
    position: relative;
    .amount-input-content {
      position: relative;
      display: flex;
      color: $color-333;
      .label {
        @include Height(50px);
        flex: 1;
        font-size: 14px;
      }
      .amount-input {
        @include Height(50px);
        font-size: 24px;
        padding-right: 14px;
      }
      .cursor {
        width: 1px;
        height: 36px;
        position: absolute;
        top: 6px;
        right: 12px;
        background-color: $mainColor;
        animation: frash 1.2s infinite ease-in-out;
      }
    }
    @keyframes frash { /*透明度由0到1*/
      0% {
        opacity: 0; /*透明度为0*/
      }
      100% {
        opacity: 1; /*透明度为1*/
      }
    }
    .amount-input-container {
      padding-left: 10px;
      background-color: $color-fff;
      position: absolute;
      z-index: 12;
      top: 15px;
      left: 15px;
      right: 15px;
      border-radius: 6px;
      .horizontal-line {
        width: 100%;
        background-color: $color-f5;
        height: 1.5px;
      }
    }
    .vip-container {
      margin-top: 130px;
      color: $color-999;
      .title {
        font-size: 14px;
        padding-left: 15px;
        margin-bottom: 12px;
      }
      .hint {
        font-size: 12px;
        padding-left: 15px;
        @include Height(40px);
      }
    }
    .register-link-container {
      width: 100%;
      position: absolute;
      bottom: 80px;
      .register-link {
        display: block;
        width: 136px;
        @include Height(44px);
        @include center-block;
        border: 1px solid $mainColor;
        border-radius: 100px;
        text-align: center;
      }
    }

    .pay-bar {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      display: flex;
      font-size: 14px;
      color: $color-fff;
      .amount-info {
        background-color: #393A3B;
        @include Height(50px);
        padding-left: 15px;
        flex: 2;
        span {
          vertical-align: middle;
        }
      }
      .pay-btn {
        background-color: $mainColor;
        font-size: 16px;
        @include Height(50px);
        text-align: center;
        flex: 1;
        &.disabled {
          opacity: .6;
        }
      }
    }
  }
</style>





