<template>
  <div class="keyboard-container" v-show="visible">
    <transition name="fade">
      <div class="mask" v-if="isShow"></div>
    </transition>
    <transition name="fade-slide">
      <div class="keyboard" v-if="isShow" :class="{disabled:!valid}">
        <div class="column">
          <div class="item num-btn" data-value="1" @touchstart.stop="touchStart" @touchend="touchEnd">1</div>
          <div class="item num-btn" data-value="4" @touchstart.stop="touchStart" @touchend="touchEnd">4</div>
          <div class="item num-btn" data-value="7" @touchstart.stop="touchStart" @touchend="touchEnd">7</div>
          <div class="item num-btn" data-value="." @touchstart.stop="touchStart" @touchend="touchEnd">.</div>
        </div>
        <div class="column">
          <div class="item num-btn" data-value="2" @touchstart.stop="touchStart" @touchend="touchEnd">2</div>
          <div class="item num-btn" data-value="5" @touchstart.stop="touchStart" @touchend="touchEnd">5</div>
          <div class="item num-btn" data-value="8" @touchstart.stop="touchStart" @touchend="touchEnd">8</div>
          <div class="item num-btn" data-value="0" @touchstart.stop="touchStart" @touchend="touchEnd">0</div>
        </div>
        <div class="column">
          <div class="item num-btn" data-value="3" @touchstart.stop="touchStart" @touchend="touchEnd">3</div>
          <div class="item num-btn" data-value="6" @touchstart.stop="touchStart" @touchend="touchEnd">6</div>
          <div class="item num-btn" data-value="9" @touchstart.stop="touchStart" @touchend="touchEnd">9</div>
          <div class="item down" @click.stop="confirm"></div>
        </div>
        <div class="column">
          <div class="item delete" @touchstart.stop="deleteNumber" @touchend="touchEnd"></div>
          <div class="item confirm" @click.stop="confirm">确定</div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'KeyBoard',
    props: {
      onClose: Function,
      valid: Boolean,
      visible: Boolean
    },
    data(){
      return {
        isShow: this.visible
      }
    },
    methods: {
      show(){
        this.isShow = true;
        this.$emit('shown');
      },
      hide(){
        this.isShow = false;
        setTimeout(()=>{
          this.$emit('hidden');
        },300);
      },
      touchStart(e){
        e.target.style.backgroundColor = "#f5f5f5";
        this.$emit('onInput', e.target.getAttribute('data-value'));
      },
      touchEnd(e){
        e.target.style.backgroundColor = "#ffffff";
      },
      deleteNumber(e){
        e.target.style.backgroundColor = "#f5f5f5";
        this.$emit('onDelete');
      },
      confirm(){
          if(this.valid){
            this.hide();
            this.$emit('onConfirm');
          }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~@/style/mixin";

  .keyboard-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    user-select: none;
    z-index: 10;
    .mask {
      position: absolute;
      opacity: 0.7;
      width: 100%;
      height: 100%;
      background-color: black;
    }
    .keyboard {
      width: 100%;
      bottom: 0;
      height: 40%;
      text-align: center;
      background-color: $color-fff;
      position: absolute;
      display: flex;
      transform: translateY(0);
      &.disabled{
        .confirm,.down{
          opacity: .6;
        }
      }
      .column {
        display: flex;
        flex: 1;
        flex-direction: column;
        .item {
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 24px;
          vertical-align: middle;
          flex: 1;
          border-top: 1px solid $color-f5;
          border-left: 1px solid $color-f5;
        }
        .down {
          background: url(../assets/down.svg) no-repeat center;
        }
        .delete {
          background: url(../assets/delete.svg) no-repeat center;
        }
        .confirm {
          background-color: $mainColor;
          color: $color-fff;
          font-size: 20px;
          &.disabled {
            opacity: .5;
          }
        }
      }
    }
    .fade-slide-enter-active, .fade-slide-leave-active, .fade-enter-active, .fade-leave-active {
      transition: all .3s
    }
    .fade-enter, .fade-leave-to {
      opacity: 0;
    }
    .fade-slide-enter, .fade-slide-leave-to {
      /*transform: translateY(100%);*/
      bottom: -50%;
    }
  }


</style>



