<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
  export default {
    name: 'app',
  };
</script>

<style>
  body {
    overflow: hidden;
  }
</style>

<style lang="scss" scoped>
  #app {
    overflow-y: scroll;
  }
</style>



