import './toast.scss';

const toast = {
  _ele: null,
  _msg: null,
  _icon: null,
  timeout: null,
  ele() {
    if (!this._ele) {
      const ele = window.document.createElement('div');
      ele.className = "toast";
      ele.innerHTML = `
        <div class="toast-container">
          <div class="icon"></div>
          <p></p>
        </div>
      `;
      window.document.body.appendChild(ele);
      this._ele = ele;
      this._msg = ele.querySelector('p');
      this._icon = ele.querySelector('.icon');
    }
    return this._ele;
  },
  show(type, msg, callback, time) {
    this.ele().className = 'toast';
    this.ele().style.display = '';
    this._icon.className = 'icon ' + type;
    this._msg.innerText = msg;
    if (typeof time === 'number') {
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.hide();
        callback && callback();
      }, time * 1000);
    }
  },
  hide() {
    this.ele().className = 'toast';
    this.ele().style.display = 'none';
  },
  loading(msg = '加载中') {
    this.show('loading', msg);
  },
  warning(msg) {
    this.show('warning', msg, null, 1.5);
  },
  success(msg, options = {}) {
    this.show('success', msg, options.callback, 1.5);
  },
};

export default toast;



