<template>
  <div class="nav-row-container" @click="doAction">
    <p class="label">{{title}}</p>
    <p class="arrow" v-show="!hideArrow"></p>
    <p class="content" :class="{ empty:!text,disabled:disabled}" >{{displayText}}</p>
  </div>
</template>

<script>
  export default {
    name: 'NavRow',
    props: {
      title: String,
      disabled: Boolean,
      text: String,
      disabledText:String,
      placeholder: String,
      noArrow: Boolean,
      action: Function
    },
    data(){
      return {}
    },
    computed: {
      hideArrow(){
        return this.disabled || this.noArrow
      },
      displayText(){
          if(this.disabled) return this.disabledText;
          return this.text || this.placeholder;
      }
    },
    methods: {
      doAction(){
        !this.disabled && this.action && this.action();
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~@/style/mixin";

  .nav-row-container {
    position: relative;
    @include Height(50px);
    font-size: 14px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: $color-fff;
    &:after {
      content: '';
      height: 1px;
      background-color: $color-f5;
      display: block;
      width: 100%;
      position: absolute;
      bottom: 0px;
      transform: scale(1, 0.5);
    }
    .label {
      color: $color-333;
      @include Height(50px);
      float: left;

    }
    .content {
      float: right;
      color: #FF5339;
      @include Height(50px);
      &.empty{
        color: $color-333;
      }
      &.disabled {
        color: $color-999;
      }
    }
    .arrow {
      float: right;
      border: 2px solid $color-999;
      border-bottom: none;
      border-left: none;
      width: 10px;
      height: 10px;
      transform: rotate(45deg);
      margin-left: 4px;
      margin-top: 20px;
    }
  }
</style>



