<template>
  <div class="page">
    <div class="coupon-content">
      <RadioRow title="不使用优惠券" :isChecked="getCheckedStatus()" @checked="updateSelectCoupon()"/>
      <div class="coupon-list">
        <div v-for="item in couponList" class="item" @click="updateSelectCoupon(item.id, item.discountValue)">
          <div class="price">
            <span>{{item.amountValue}}</span>
            {{getCouponType(item.type)}}
          </div>
          <div class="content">
            <p class="title">{{item.title}}</p>
            <p class="time">{{getDateTime(item.beginTime)}}-{{getDateTime(item.endTime)}}</p>
            <p class="limit">{{item.condition}}</p>
            <img class="icon" :src="getCheckedStatus(item.id)?checkedImg:unCheckedImg"/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        couponList: this.$store.state.couponList,
        checkedImg: require('@/assets/radio-checked.svg'),
        unCheckedImg: require('@/assets/radio-unchecked.svg')
      };
    },
    methods: {
      updateSelectCoupon(id = ""){
        this.$store.commit('setCouponId', id);
        setTimeout(()=>{
          this.$router.go(-1);
        },200);
      },
      getCouponType(type){
        return type === "CASH" ? "元" : "折"
      },
      getCheckedStatus(id = ""){
        return id === this.$store.state.couponId;
      },
      getDateTime(date){
        return new Date(Number(date)).format("yyyy-MM-dd");
      }
    }
  };
</script>

<style lang="scss" scoped>
  @import "~@/style/mixin";

  .page {
    height: 100%;
    background-color: $color-f5;
    .coupon-content {
      padding: 15px 18px;
    }
    .coupon-list {
      margin-top: 20px;
      .item {
        height: 94px;
        display: flex;
        flex-direction: row;
        background-color: #fff;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        overflow: hidden;
        border-left: 4px solid #3CACFF;
        margin-bottom: 15px;

        .price {
          flex-grow: 0;
          flex-shrink: 0;
          width: 97px;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          font-size: 14px;
          color: #333;
          border-right: 1px dashed #E6E4E3;
          position: relative;

          &::after {
            content: ' ';
            display: block;
            width: 8px;
            height: 8px;
            background-color: $color-f5;
            position: absolute;
            right: -4px;
            bottom: -4px;
            border-radius: 50%;
          }

          &::before {
            content: ' ';
            display: block;
            width: 8px;
            height: 8px;
            background-color: $color-f5;
            position: absolute;
            right: -4px;
            top: -4px;
            border-radius: 50%;
          }

          span {
            font-size: 34px;
            margin-right: 4px;
          }
        }

        .content {
          flex-grow: 1;
          flex-shrink: 1;
          padding: 0 15px;
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          .icon {
            position: absolute;
            right: 20px;
          }
          &::after {
            content: ' ';
            display: block;
            width: 5px;
            height: 100%;
            background: url(../assets/coupon-border.svg) repeat-y center top;
            background-size: 5px;
            position: absolute;
            top: 0;
            right: 0;
          }

          .title {
            color: #333;
            font-size: 16px;
          }

          .time {
            color: #999;
            font-size: 12px;
            margin-top: 7px;
          }

          .limit {
            color: $color-666;
            margin-top: 11px;
            font-size: 14px;
          }
        }
      }
    }
  }
</style>



