    <p class="cursor" v-show="showCursor"></p>
  </div>
</template>

<script>
  export default {
    name: 'AmountInput',
    props: {
      title: String,
    },
    computed: {
    },
    data(){
        return {
            showCursor:false
        }
    },
    methods: {
        foucsed(){
            this.show
        }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~@/style/mixin";
  .amount-input-content{
    position: relative;
    display: flex;
    color: $color-333;
    .label{
      @include Height(50px);
      flex: 1;
      font-size: 14px;
    }
    .amount-input{
      @include Height(50px);
      font-size: 24px;
      padding-right: 14px;
    }
    .cursor{
      width: 1px;
      height: 36px;
      position: absolute;
      top: 6px;
      right: 12px;
      background-color: $mainColor;
      animation: frash 1.2s infinite ease-in-out;
