if (!Number.prototype.toYuan) {
  Number.prototype.toCent = function () {
    return this * 100;
  };
  Number.prototype.toYuan = function (format = false) {
    if(format) return (this / 100).toFixed(2);
    return this/100 ;
  };
  Number.prototype.toFormatYuan = function (from = 'cent') {
    if (from === 'cent') {
      return this.toYuan().replace(/(\d)(?=(\d{3})+\.)/g, '$1,');
    }
  };
}



