<template>
  <div class="radio-row-container" @click="radioClicked">
    <span class="tag" v-if="tag">{{tag}}</span>
    <p class="label">
      {{title}}
      <slot></slot>
    </p>
    <p class="content">
      {{text}}
      <img class="icon" :src="checkedStatus"/>
    </p>
  </div>
</template>

<script>
  export default {
    name: 'RadioRow',
    props: {
      title: String,
      tag: String,
      text: String,
      isChecked: Boolean,
      disabled: Boolean
    },
    data(){
      return {
        checkedImg: require('@/assets/radio-checked.svg'),
        unCheckedImg: require('@/assets/radio-unchecked.svg'),
        disabledImg: require('@/assets/radio-disabled.svg')
      }
    },
    computed: {
        checkedStatus(){
            if(this.disabled) return this.disabledImg;
            if(this.isChecked) return this.checkedImg;
            return this.unCheckedImg;
        }
    },
    methods: {
      radioClicked(){
          this.$emit('checked');
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~@/style/mixin";

  .radio-row-container {
    position: relative;
    @include Height(50px);
    font-size: 14px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: $color-fff;
    color: $color-333;
    &:after {
      content: '';
      height: 1px;
      background-color: $color-f5;
      display: block;
      width: 100%;
      position: absolute;
      bottom: 0px;
      transform: scale(1, 0.5);
    }
    .label {
      @include Height(50px);
      float: left;
      span{
        color: red !important;
      }
    }
    .tag{
      float: left;
      font-size: 12px;
      height: 24px;
      line-height: 20px;
      border:1px solid #FF5339;
      border-radius: 4px;
      margin-top: 13px;
      margin-right: 4px;
      padding: 2px;
      color: #FF5339;
    }
    .content {
      float: right;
      @include Height(50px);
    }
    .icon{
      vertical-align: middle;
      margin-left: 8px;
    }
  }
</style>



