import axios from 'axios';
import toast from './toast';

const qs = require('qs');

axios.defaults.timeout = 60 * 1000;
axios.defaults.withCredentials = true;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

/**
 * ajax请求
 * @param {String} url 请求地址
 * @param {Object} data 请求数据
 * @param {Object} config 配置项
 * @param {Boolean} config.origin 是否需要直接返回response, 后台接口返回格式与默认不同的情况下使用
 * @param {Boolean} config.loading 是否需要loading, 默认为true
 * @param {Boolean} config.error 是否需要error, 默认为true
 * @param {...} {...config} ajax请求的默认设置, 具体请参考Axios
 * @returns {Promise}
 */
function request(url, data = {}, config = {}) {
  url = window.host + '/gatewayWeb.htm?command=' + url;
  if (data instanceof FormData) {
    config.contentType = false; // 上传文件时必要参数
    config.processData = false; // 上传文件时必要参数
    config.method = 'post';
    // 用qs序列化之后, 后台才可以取到
    // https://segmentfault.com/q/1010000007607111
    config = {url: url || confg.url, data: qs.stringify(data || confg.data), ...config,};
  } else {
    config.method = 'post';
    config = {url: url || confg.url, data: qs.stringify(data || confg.data), ...config,};
  }
  toast.loading();
  return new Promise((resolve, reject) => {
    axios(config).then(response => {
      config.loading === false || toast.hide();
      if (config.origin) {
        return resolve(response);
      }
      response = response.data;

      if (!response || !response.result) {
        config.error === false || toast.warning('网络请求错误');
        reject('网络请求错误');
        return;
      }

      const result = response.result;
      if (!result.success) {
        config.error === false || toast.warning(result.errorMsg);
        reject(result.errorMsg);
        return;
      }

      resolve(response.data);
    }).catch(error => {
      config.error === false || toast.warning(error);
      reject(error);
    });
  });
}

/**
 * 把null转为'', object || array 转为json
 * @param obj
 * @returns {{}}
 */
function format(obj) {
  if (!obj) {
    return;
  }

  let copy = {...obj};
  for (let key in copy) {
    if (!copy[key]) {
      copy[key] = '';
    } else if (typeof copy[key] !== 'string') {
      copy[key] = JSON.stringify(copy[key]);
    }
  }
  return copy;
}

export default {request};



