import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  state: {
    couponId: "",
    couponName: '',
    couponList:[]
  },
  mutations: {
    setCouponId(state, newValue) {
      state.couponId = newValue;
    },
    setCouponName(state, newValue){
      state.couponName = newValue;
    },
    setCouponList(state, newValue){
      state.couponList = newValue;
    }
  },
});



